import React from 'react';

const SearchBox = ({nome, searchfield, onsearchChange }) => {
	let messaggio = "";
	if(nome==='Broccola'||nome==='broccola'){
		messaggio = `Ciao Broccola `;
	}else{
		messaggio =`Ciao ${nome}`;
	}


	return(
		
		<input 
			id="barracerca"
			className="tc pa3 ba b--green bg-lightest-blue"
			type="search" 
			placeholder={messaggio}
			onChange={onsearchChange}
			
		/>
		
	);
}

export default SearchBox;


